@import "../../Mixins";
.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 60px 0 0;
  &>a{
    align-self: center;
    &:hover {
      opacity: 100%;
      border-bottom: none;
    }
    &>h2 {
      margin-bottom: 16px;
    }
  }
}
.subtitle {
  color: white;
  font-size: 18px;
  margin-bottom: 24px;
  align-self: center;
  text-align: center;
}
